import { Component } from "react";
import { CardList } from "./components/card-list/card-list.component";
import { SearchBox } from "./components/serach-box/search-box.component";
import "./styles.css";

class App extends Component {
  constructor() {
    super();

    this.state = {
      People: [],
      searchField: ""
    };
  }

  componentDidMount() {
    fetch("https://jsonplaceholder.typicode.com/users")
      .then((response) => response.json())
      .then((users) => this.setState({ People: users }));
  }

  handleChange = (e) => {
    this.setState({ searchField: e.target.value });
  };

  render() {
    const { People, searchField } = this.state;
    const filteredPeople = People.filter((People) =>
      People.name.toLowerCase().includes(searchField.toLowerCase())
    );
    return (
      <div className="App">
        <h1 className="header">User List</h1>
        <SearchBox
          placeholder="search for people"
          handleChange={this.handleChange}
        />
        <CardList People={filteredPeople}></CardList>
      </div>
    );
  }
}

export default App;
